<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('SetFormMapping') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="ManageRuleEngineTarget">
                            <div class="float-right">
                                <button type="button" class="btn btn-info" v-on:click="addMoreOptionToBindForm()"><i
                                        class="fa fa-plus pr-2"></i>{{ $t('AddMore') }}</button>&nbsp;
                                <button type="button" class="btn btn-success" v-on:click="SaveFormMapping()"
                                    v-if="StatuscodeMappingForBindForm.length > 0"><i class="fa fa-save pr-2"></i>{{
                                        $t('Save') }}</button>&nbsp;
                                <button type="button" class="btn btn-danger" v-on:click="Close"><i
                                        class="fa fa-times pr-2"></i> {{ $t('Cancel') }}</button>
                            </div>
                            <div class="listing mt-3">
                                <div class="table-responsive" style="height:300px;">
                                    <table id="bindFormWithStatusCode"
                                        class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                        <thead class="thead-bg">
                                            <tr class="text-capitalize">
                                                <th scope="col" v-if="Form == 'FormBulider'"><span style="width:200px;">{{
                                                    $t('ParentWorkType') }}
                                                    </span></th>
                                                <th scope="col" v-if="CheckLocate == 'locating'"><span style="width:200px;" >
                                                        {{ $t('ParentStatusCode') }}</span> </th>
                                                <th scope="col" v-if="CheckLocate == 'locating'"><span style="width:200px;" >
                                                        {{ $t('StatusCode') }} </span></th>
                                                <th scope="col"><span style="width:200px;"> {{ $t('Form') }}</span></th>
                                                <th scope="col"><span style="width:150px;"> {{ $t('IsFormMandatory')
                                                }}</span></th>
                                                <th scope="col" style="display:none;"><span
                                                        style="width:200px;display:none;"> {{ $t('Subject') }}</span></th>
                                                <th scope="col" style="display:none;"><span
                                                        style="width:200px;display:none;"> {{ $t('NotificationTo') }}</span>
                                                </th>
                                                <th scope="col"><span style="width:150px;"> {{ $t('ApprovalTo') }}</span>
                                                </th>
                                                <th class="text-center" scope="col"><span style="width:150px;">{{
                                                    $t('Action') }}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="StatuscodeMappingForBindForm.length == 0">
                                                <td colspan="8" class="text-center text-danger">{{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="(item, index) in StatuscodeMappingForBindForm" :key="index">
                                                <td class="text-capitalize" v-if="Form == 'FormBulider'">
                                                    <Field :name="'WorkType' + index" v-slot="{ errors }"
                                                        :rules="{ 'required': true }">
                                                        <select class="form-control" v-model="item.selectedWorkType"
                                                            v-bind:class="{ 'is-invalid': !item.isValid }"
                                                            @change="getFormList('FSM_STATUSCODE', null, item, true)">
                                                            <option value="">{{ $t('Select') }}</option>
                                                            <option v-for="(option, i) in WorkTypeDropdownOptions"
                                                                v-bind:value="option.value" :key="i">{{ option.name }}
                                                            </option>
                                                        </select>
                                                        <span v-if="errors[0]" :name="'WorkType' + index"
                                                            class="invalid-feedback d-block">{{ $t('WorkTypeIsRequired')
                                                            }}</span>
                                                           
                                                    </Field>
                                                </td>
                                                <td class="text-capitalize" v-if="CheckLocate == 'locating'">
                                                    <Field :name="'StatusCode' + index" v-slot="{ errors }"
                                                        :rules="{ 'required': true }">
                                                        <select class="form-control" v-model="item.parentselectedStatusCode"
                                                            v-bind:class="{ 'is-invalid': !item.isValid }"
                                                            @change="getFormList('FSM_SUBSTATUSCODE', null, item, true)">
                                                            <option value="">{{ $t('Select') }}</option>
                                                            <option v-for="(option, i) in ParentStatusCodeDropdownOptions"
                                                                v-bind:value="option.value" :key="i">{{ option.name }}
                                                            </option>
                                                        </select>
                                                        <span v-if="errors[0]" :name="'StatusCode' + index"
                                                            class="invalid-feedback d-block">{{ $t('ParentSCode') }}</span>
                                                           
                                                    </Field>
                                                </td>
                                                <td class="text-capitalize" v-if="CheckLocate == 'locating'">
                                                    <select class="form-control" v-model="item.selectedStatusCode"
                                                        v-bind:class="{ 'is-invalid': !item.isValid }">
                                                        <option value="0">{{ $t('Select') }}</option>
                                                        <option v-for="(option, i) in item.subStatusCodeList"
                                                            v-bind:value="option.value" :key="i">{{ option.name }}</option>
                                                    </select>
                                                   
                                                </td>
                                                <td style="position:relative; overflow: visible !important;">
                                                    <multiselect v-model="item.selectedForm" placeholder="Select Form"
                                                        label="name" track-by="value" v-bind:options="FormList"
                                                        v-bind:multiple="true"
                                                        v-bind:class="{'disabled':true, 'border border-danger': !item.isValid }"
                                                        v-bind:taggable="true"></multiselect>
                                                    <span v-if="!item.isValid" class="text-danger">{{
                                                        $t('AlreadySelectedStatusCode') }}</span>
                                                </td>
                                                <td class="text-center">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input class="chkitems custom-control-input" type="checkbox" :name="'policySLAReminderResolve_IsHOD' + index"
                                                            :id="'policySLAReminderResolve_IsHOD'+index"
                                                            v-model="item.isFormMandatory" />
                                                        <label type="checkbox" class="custom-control-label" :name="'policySLAReminderResolve_IsHOD' + index"
                                                            :for="'policySLAReminderResolve_IsHOD'+index"></label>
                                                    </div>
                                                </td>
                                                <td style="display:none;">
                                                    <input type="text" class="form-control" v-model="item.subject"
                                                        placeholder="Subject" />
                                                </td>
                                                <td style="display:none;">
                                                    <input type="text" class="form-control" v-model="item.notificationTo"
                                                        placeholder="Email" />
                                                </td>
                                                <td class="text-center">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input class="chkitems custom-control-input" type="checkbox"
                                                            :id="'policySLAReminderResolve'+index" :name="'policySLAReminderResolve' + index"
                                                            v-model="item.isApprovalRequired"
                                                            v-on:change="chkLocateApprovaStatus($event, index)" />
                                                        <label type="checkbox" class="custom-control-label" :name="'policySLAReminderResolve' + index"
                                                            :for="'policySLAReminderResolve'+index"></label>
                                                    </div>
                                                </td>
                                                <td class="text-capitalize" v-if="item.isApprovalRequired">
                                                    <select class="form-control ApprovalTo"
                                                        v-bind:id="'isLocateApprovalChk_' + index" name="StatusList"
                                                        v-model="item.approval_to">
                                                        <option value="0">{{ $t('SelectApprovalTo') }}</option>
                                                        <option value="HOD">HOD</option>
                                                        <option value="Supervisor">Supervisor</option>
                                                        <option value="Admin">Admin</option>
                                                    </select>
                                                </td>
                                                <td class="text-capitalize" v-else>
                                                    <select class="form-control ApprovalTo  d-none"
                                                        v-bind:id="'isLocateApprovalChk_' + index" name="StatusList"
                                                        v-model="item.approval_to">
                                                        <option value="">{{ $t('SelectApprovalTo') }}</option>
                                                        <option value="HOD">HOD</option>
                                                        <option value="Supervisor">Supervisor</option>
                                                        <option value="Admin">Admin</option>
                                                    </select>
                                                </td>
                                                <td class="text-center single-action">
                                                    <a title="Delete" v-if="index > 0"
                                                        v-on:click="deleteRecordOfFormMapping(index)"><i
                                                            v-bind:class="{ 'fa fa-trash text-danger action-icon': true }"></i></a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        Form_Id: {
            type: Number,
            Required: true
        },
        IsCheckLocate: {
            type: String,
            Required: true
        },
        Form: {
            type: Number,
            Required: true
        }
    },
    data() {
        return {
            isLoading: false,
            StatuscodeMappingForBindForm: [],
            parentselectedStatusCode: "",
            ParentStatusCodeDropdownOptions: [],
            StatusCodeDropdownOptions: [],
            FormList: [],
            approval_To: "",
            workTypeId: null,
            CheckLocate: '',
            WorkTypeDropdownOptions: [],
            selectedWorkType: "",
        }
    },
    created: async function () {
        debugger
        var vm = this;
        vm.CheckLocate = vm.IsCheckLocate;
        vm.workTypeId = vm.Form_Id;
        if(vm.Form == 'FormBulider'){
            this.getFormList('FSM_WORKTYPES', null, null, null);
            this.getFormList('FSM_FORMS', null, null, null);
        }else{
            this.getFormList('FSM_STATUSCODE', vm.workTypeId, null, null);
            this.getFormList('FSM_FORMS', null, null, null);
        } 
    },
    methods: {
        getFormList: async function (type, refId, item, onchange) {
            var vm = this;
            if (vm.Form == 'FormBulider') {
                if (item != null) {
                    var url = `id=${item.parentselectedStatusCode}&&moduleName=${'Ticketing'}&type=${type}&search=&refId=${item.selectedWorkType}`;
                }               
                else {
                    var url = `id=&&moduleName=${'Ticketing'}&type=${type}&search=&refId=`;
                }
            }
            else {
                if (item != null) {
                    var url = `id=${item.parentselectedStatusCode}&&moduleName=${'Ticketing'}&type=${type}&search=&refId=${vm.workTypeId}`;
                }
                else if (refId != null) {
                    var url = `id=&&moduleName=${'Ticketing'}&type=${type}&search=&refId=${refId}`;
                }
                else {
                    var url = `id=&&moduleName=${'Ticketing'}&type=${type}&search=&refId=`;
                }
            }          
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.status == 200 || response.statusText == 'OK') {
                    if (type == 'FSM_FORMS') {                       
                        vm.FormList = response.data.DATA;
                        vm.getStatuscodeMappingForBindForm();
                    }
                    else if (type == 'FSM_STATUSCODE') {
                        vm.ParentStatusCodeDropdownOptions = response.data.DATA;
                    }
                    else if (type == 'FSM_WORKTYPES') {
                        vm.WorkTypeDropdownOptions = response.data.DATA;
                    }
                    else {
                        item.subStatusCodeList = response.data.DATA;
                        if (onchange == true) {
                            item.selectedStatusCode = 0;
                        }
                    }
                }
                else {
                    if (type == 'FSM_FORMS') {
                        vm.FormList = [];
                    }
                    else if (type == 'FSM_STATUSCODE') {
                        item.ParentStatusCodeDropdownOptions = [];
                        if (onchange == true) {
                            item.parentselectedStatusCode = "";
                        }
                    }
                    else if (type == 'FSM_WORKTYPES') {
                        vm.WorkTypeDropdownOptions = [];
                    }
                    else {
                        item.subStatusCodeList = [];
                        if (onchange == true) {
                            item.selectedStatusCode = 0;
                        }
                    }
                }
            });
        },
        chkLocateApprovaStatus: function (event, index) {
            var vm = this;
            var chlLocateApporaval = event.target.checked;
            if (chlLocateApporaval == true) {
                $('#isLocateApprovalChk_' + index).removeClass('d-none').addClass('d-block');
            }
            else {
                $('#isLocateApprovalChk_' + index).removeClass('d-block').addClass('d-none');
            }
        },
        addMoreOptionToBindForm: function () {         
            var vueObj = this;
            let selectedFrm = vueObj.FormList.filter(x => x.value == vueObj.workTypeId);
            debugger
            var obj = {
                selectedWorkType:'',
                parentselectedStatusCode: '',
                selectedStatusCode: 0,
                selectedForm: selectedFrm,
                subject: "",
                notificationTo: "",
                sendNotificationToYard: false,
                isApprovalRequired: false,
                isFormMandatory: false,
                isValid: true,
                subStatusCodeList: [],
                approval_To: 0,
            }
            vueObj.StatuscodeMappingForBindForm.push(obj);            
           // setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
        },
        deleteRecordOfFormMapping: function (i) {
            debugger
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Record")), function () {
                vm.StatuscodeMappingForBindForm.splice(i, 1);
            });
        },
        getStatuscodeMappingForBindForm: function () {
            debugger
            var vueObj = this;
            if (vueObj.Form == 'FormBulider') {
                var objectPostString = `worktypeid=&formId=${vueObj.workTypeId}`;
            } else {
                var objectPostString = `worktypeid=${vueObj.workTypeId}&formId=`;
            }
            DataServices.GetFsmWorktypeStatusCodeFormMappingList(objectPostString).then(response => {
                if (response.data != "" && response.data != null) {
                    let selectedForm = [];
                    if (response.data.result.data.length > 0) {
                        debugger
                        $.each(response.data.result.data, function (index, item) {
                            var returnedData = $.grep(response.data.result.data, function (element, index) {
                                return element.subject == item.subject
                                    && element.email == item.email
                                    && element.is_approval_required == item.is_approval_required
                                    && element.is_notification_to_yard == item.is_notification_to_yard
                                    && element.is_form_mandatory == item.is_form_mandatory
                                    && element.sub_status_code_id == item.sub_status_code_id
                                    && element.status_code_id == item.status_code_id
                                    && element.approval_to == item.approval_to;
                            });
                            if (returnedData.length > 0) {
                                debugger
                                var checkIfExists = $.grep(vueObj.StatuscodeMappingForBindForm, function (element, index) {
                                    return element.subject == item.subject
                                        && element.notificationTo == item.email
                                        && element.isApprovalRequired == item.is_approval_required
                                        && element.sendNotificationToYard == item.is_notification_to_yard
                                        && element.isFormMandatory == item.is_form_mandatory
                                        && element.approval_to == item.approval_to
                                        && parseInt(element.parentselectedStatusCode) == parseInt(item.status_code_id)
                                        && parseInt(element.selectedStatusCode) == parseInt(item.sub_status_code_id)
                                        && parseInt(element.selectedWorkType) == parseInt(item.worktype_id);
                                });
                                //debugger
                                if (checkIfExists.length == 0) {
                                    $.each(returnedData, function (frmIndex, frm) {
                                        selectedForm=[];
                                        var selectedFrm = vueObj.FormList.filter(function (x) { return x.value == frm.form_id });
                                        selectedForm.push(...selectedFrm);                                        
                                    });
                                    //debugger
                                    var obj = {
                                        parentselectedStatusCode: item.status_code_id,
                                        selectedStatusCode: item.sub_status_code_id,
                                        selectedForm: selectedForm,
                                        subject: item.subject,
                                        notificationTo: item.email,
                                        sendNotificationToYard: item.is_notification_to_yard,
                                        isApprovalRequired: item.is_approval_required,
                                        isFormMandatory: item.is_form_mandatory,
                                        approval_to: item.approval_to,
                                        isValid: true,
                                        subStatusCodeList: [],
                                        selectedWorkType: item.worktype_id
                                    };  
                                    selectedForm=[];                         
                                    vueObj.StatuscodeMappingForBindForm.push(obj);
                                }
                                if(vueObj.Form=='FormBulider'){
                                        vueObj.getFormList('FSM_STATUSCODE', null, obj, null);
                                        vueObj.getFormList('FSM_SUBSTATUSCODE', null, obj, null);
                                    }else{
                                        vueObj.getFormList('FSM_SUBSTATUSCODE', null, obj, null);
                                   }    
                                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                            }
                        });
                    }
                    else {
                       // debugger
                        vueObj.StatuscodeMappingForBindForm = [];
                    }
                }
                else {
                   // debugger
                    vueObj.StatuscodeMappingForBindForm = [];
                }
            });
        },
        Close: function () {
            this.$parent.SetFormMapping = false;
        },
        SaveFormMapping: function () {
            var vueObj = this;
            var data = [];
            var IsValid = true;
            if (vueObj.StatuscodeMappingForBindForm.length > 0) {
                $.each(vueObj.StatuscodeMappingForBindForm, function (index, item) {
                    $.each(vueObj.StatuscodeMappingForBindForm, function (sIndex, sItem) {
                        if (index != sIndex) {
                            if(vueObj.Form == 'FormBulider'){
                                if (item.selectedWorkType == sItem.selectedWorkType && item.parentselectedStatusCode == sItem.parentselectedStatusCode && item.selectedStatusCode == sItem.selectedStatusCode ) {
                                    vueObj.ShowAlert(vueObj.$t("AlreadySelectedStatusCode", [vueObj.$t("StatusCode")]), "failure", true, vueObj.$t("Alert"));
                                    vueObj.StatuscodeMappingForBindForm[sIndex].isValid = false;
                                    IsValid = false;
                                }
                            }else{
                                $.each(item.selectedForm, function (aIndex, aItem) {
                                $.each(sItem.selectedForm, function (eIndex, eItem) {
                                    if (item.selectedStatusCode == sItem.selectedStatusCode && aItem.value == eItem.value) {
                                        vueObj.ShowAlert(vueObj.$t("AlreadySelectedStatusCode", [vueObj.$t("StatusCode")]), "failure", true, vueObj.$t("Alert"));
                                        vueObj.StatuscodeMappingForBindForm[sIndex].isValid = false;
                                        IsValid = false;
                                    }
                                });
                            });
                            }                            
                        }
                    });
                });
                if (IsValid) {
                    $.each(vueObj.StatuscodeMappingForBindForm, function (index, item) {
                        debugger
                        var FormArr = [];
                        var forms = "";
                        $.each(item.selectedForm, function (ind, itm) {
                            FormArr.push(itm.value);
                        });
                        if (FormArr.length > 0) {
                            forms = FormArr.join(",");
                        }
                        var dataObject = {
                            selectedStatusCode: item.parentselectedStatusCode,
                            selectedForm: forms,
                            subject: item.subject,
                            notificationTo: item.notificationTo,
                            sendNotificationToYard: item.sendNotificationToYard,
                            isApprovalRequired: item.isApprovalRequired,
                            isFormMandatory: item.isFormMandatory,
                            workTypeId: item.selectedWorkType,
                            sub_status_code_id: item.selectedStatusCode,
                            approval_To: item.approval_to
                        };
                        data.push(dataObject);
                    });
                    var objectPostString = JSON.stringify(data);
                    vueObj.$refs.ManageRuleEngineTarget.validate().then(result => {
                        if (result) {
                            DataServices.SaveFormStatusCodeMapping(objectPostString).then((response) => {
                                if (response.data.status == 'Success') {
                                    vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("WorktypeFormMapping")]), "success", true, vueObj.$t("Alert"));
                                    vueObj.Close();
                                    vueObj.$parent.FetchRecords();
                                }
                                else {
                                    vueObj.ShowAlert(vueObj.$t("AddedError", [vueObj.$t("Resource")]), "failure", true, vueObj.$t("Alert"));
                                    vueObj.Close();
                                }
                            });
                        }
                    });
                }
            }
            else {
                vueObj.ShowAlert(vueObj.$t("AddedError", [vueObj.$t("Resource")]), "failure", true, vueObj.$t("Alert"));
            }
        }
    }
}
</script>

<style>.single-action i {
    font-size: 20px;
}</style>